import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Zoom from 'react-reveal/Zoom';
function Categories({ setSelectedCategory }) {
  const [selectedCategory, setSelectedCategoryState] = useState(null); // Define the state variable

  const categoryData = [
    {
      categoryName: "Mobile",
      categoryPhoto: "https://i.ibb.co/xHFpQTV/product-cat-2.png",
    },
    {
      categoryName: "Tablet",
      categoryPhoto: "https://ipsw.me/assets/devices/iPad14,5.png",
    },
    {
      categoryName: "Smartwatch",
      categoryPhoto: "https://i.ibb.co/g3YK8H2/product-cat-4.png",
    },
    {
      categoryName: "Laptop",
      categoryPhoto: "https://i.imgur.com/2fddkuS.png",
    },
  ];

  const navigate = useNavigate();

  const handleCategoryClick = (categoryName) => {
    setSelectedCategoryState(categoryName); // Update the state

    // Navigate to the /shop route with the selected category as a query parameter
    navigate(`/shop?category=${categoryName}`);
  };

  return (
    <div className="categories-container cont">
      {categoryData.map((category, index) => (
        <div
          className={`category ${
            selectedCategory === category.categoryName ? "clicked" : ""
          }`}
          key={index}
        >
          <Zoom bottom>
            <a
              href="#"
              className="image"
              onClick={() => handleCategoryClick(category.categoryName)}
            >
              <img src={category.categoryPhoto} alt={category.categoryName} />
            </a>
            <div className="text">
              <h3>
                <a href="">{category.categoryName}</a>
              </h3>
            </div>
          </Zoom>
        </div>
      ))}
    </div>
  );
}

export default Categories;
