import React, { useState } from "react";
import emailjs from "emailjs-com";

function Form() {
  const [message, setMessage] = useState(""); // State for user message

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_7d4bbul",
        "template_kani98j",
        e.target,
        "YQP55Kw0tXoUx0Wn6"
      )
      .then(
        (result) => {
          setMessage("Message sent successfully!"); // Set success message
        },
        (error) => {
          setMessage("Error sending message. Please try again."); // Set error message
          console.error(error);
        }
      );

    // Reset the form fields after submission
    e.target.reset();
  };

  return (
    <form className="form" onSubmit={handleSubmit}>
      {message && <div>{message}</div>}
      <div className="flex">
        <label>
          <input
            required=""
            placeholder="First Name"
            type="text"
            className="input"
            name="firstName"
          />
        </label>
        <label>
          <input
            required=""
            placeholder="Last Name"
            type="text"
            className="input"
            name="lastName"
          />
        </label>
      </div>
      <label>
        <input
          required=""
          placeholder="Email"
          type="email"
          className="input"
          name="email"
        />
      </label>
      <label>
        <input
          required=""
          type="tel"
          placeholder="Contact Number"
          className="input"
          name="contactNumber"
        />
      </label>
      <label>
        <textarea
          required=""
          rows="6"
          placeholder="Message"
          className="input01"
          name="message"
        ></textarea>
      </label>
      <button type="submit">Send Message</button>
    </form>
  );
}

export default Form;
