import React, { useState, useEffect } from "react";
import { Widget, addResponseMessage } from "react-chat-widget";
import "react-chat-widget/lib/styles.css";

function ChatBot() {
  useEffect(() => {
    // Simulate an initial greeting message
    addResponseMessage("Hello! How can I assist you today?");
  }, []);

  const handleNewUserMessage = (newMessage) => {
    // You can implement your chatbot logic here to process user messages and provide responses.
    // For this example, let's simulate a simple auto-reply.
    setTimeout(() => {
      addResponseMessage(
        "This is an automated response. Your message was: " + newMessage
      );
    }, 800);
  };

  return (
    <Widget
      title="Chatbot"
      subtitle="Ask me anything"
      handleNewUserMessage={handleNewUserMessage}
    />
  );
}

export default ChatBot;
