import React, { useEffect } from "react";
import { Link } from "react-router-dom";
function Checkout({ cart,updateCard}) {
  // Replace with your PayPal Client ID
  const CLIENT_ID = "AZEivPACOh29oUIXN0B0eIlQiB785LnNVYZPy2WZxzhT8Vk_9Ylh3G5qtnD3u74niBHcHwiyq4fGZtq9";

  const calculateTotalPrice = () => {
    return cart.reduce((acc, product) => {
      if (typeof product.price === 'string') {
        const priceWithoutDollarSign = product.price.replace('$', '');
        const priceAsFloat = parseFloat(priceWithoutDollarSign);
        if (!isNaN(priceAsFloat)) {
          return acc + priceAsFloat * product.quantity; // Multiply by quantity
        }
      }
      return acc;
    }, 0);
  };

  useEffect(() => {
    // Load the PayPal JavaScript SDK with the client ID
    const script = document.createElement("script");
    script.src = `https://www.paypal.com/sdk/js?client-id=${CLIENT_ID}`;
    script.async = true;

    script.onload = initializePayPalButton;

    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  const initializePayPalButton = () => {
    // Render the PayPal button
    window.paypal
      .Buttons({
        createOrder: (data, actions) => {
          return actions.order.create({
            purchase_units: [
              {
                amount: {
                  value: calculateTotalPrice().toFixed(2), // Calculate the payment amount
                },
              },
            ],
          });
        },
        onApprove: (data, actions) => {
          return actions.order.capture().then((details) => {
            alert("Payment completed!");
          });
        },
      })
      .render("#paypal-button-container");
  };


  return (
    <div className="checkout">
    <section class="py-5">
      <div class="container px-4 px-lg-5 my-5">
        <div class="row">
          <div class="col-md-4 order-md-2 mb-4">
            <h4 class="d-flex justify-content-between align-items-center mb-3">
              <span class="text-muted">Your cart</span>
              <span class="badge badge-secondary badge-pill">
                {cart.length}
              </span>
            </h4>
            <ul class="list-group mb-3 products-list">
              {cart.map((product) => (
                <li
                  key={product.id}
                  class="list-group-item d-flex justify-content-between lh-condensed product-cart"
                >
                  <div className="text d-flex flex-column">
                  <h6 class="my-0">{product.modelName}</h6>
                  <span class="text-muted">{product.price}</span>
                  <span>Quantity :{product.quantity}</span>
                  </div>
                <img src={product.image} alt="" />
                </li>
              ))}
              {/* Calculate the total price based on cartItems */}
              <li class="list-group-item d-flex justify-content-between">
                <span>Total (USD)</span>
                <strong>${calculateTotalPrice().toFixed(2)}</strong>
              </li>
            </ul>

            <form class="card p-2">
              <div class="input-group">
                <input
                  type="text"
                  class="form-control me-2"
                  placeholder="Promo code"
                />
                <div class="input-group-append">
                  <button
                    type="button"
                    class="btn btn-dark px-4 rounded-pill"
                  >
                    Redeem
                  </button>
                </div>
              </div>
            </form>
          </div>
          <div class="col-md-8 order-md-1">
            <h4 class="mb-3">Billing address</h4>
            <form class="needs-validation" novalidate>
              <div class="row">
                <div class="col-md-6 mb-3">
                  <label for="firstName" class="form-label">
                    First name
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="firstName"
                    placeholder=""
                    value=""
                    required
                  />
                  <div class="invalid-feedback">
                    Valid first name is required.
                  </div>
                </div>
                <div class="col-md-6 mb-3">
                  <label for="lastName" class="form-label">
                    Last name
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="lastName"
                    placeholder=""
                    value=""
                    required
                  />
                  <div class="invalid-feedback">
                    Valid last name is required.
                  </div>
                </div>
              </div>
              <div class="mb-3">
                <label for="username" class="form-label">
                  Username
                </label>
                <div class="input-group">
                  <div class="input-group-prepend">
                    <span class="input-group-text">@</span>
                  </div>
                  <input
                    type="text"
                    class="form-control"
                    id="username"
                    placeholder="Username"
                    required
                  />
                  <div class="invalid-feedback">
                    Your username is required.
                  </div>
                </div>
              </div>
              <div class="mb-3">
                <label for="email" class="form-label">
                  Email <span class="text-muted">(Optional)</span>
                </label>
                <input
                  type="email"
                  class="form-control"
                  id="email"
                  placeholder="you@example.com"
                />
                <div class="invalid-feedback">
                  Please enter a valid email address for shipping updates.
                </div>
              </div>
              <div class="mb-3">
                <label for="address" class="form-label">
                  Address
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="address"
                  placeholder="1234 Main St"
                  required
                />
                <div class="invalid-feedback">
                  Please enter your shipping address.
                </div>
              </div>
              <div class="mb-3">
                <label for="address2" class="form-label">
                  Address 2 <span class="text-muted">(Optional)</span>
                </label>
                <input
                  type="text"
                  class="form-control"
                  id="address2"
                  placeholder="Apartment or suite"
                />
              </div>
              <div class="row">
                <div class="col-md-5 mb-3">
                  <label for="country" class="form-label">
                    Country
                  </label>
                  <select
                    class="form-select d-block w-100"
                    id="country"
                    required
                  >
                    <option value="">Choose...</option>
                    <option>United States</option>
                  </select>
                  <div class="invalid-feedback">
                    Please select a valid country.
                  </div>
                </div>
                <div class="col-md-4 mb-3">
                  <label for="state" class="form-label">
                    State
                  </label>
                  <select
                    class="form-select d-block w-100"
                    id="state"
                    required
                  >
                    <option value="">Choose...</option>
                    <option>California</option>
                  </select>
                  <div class="invalid-feedback">
                    Please provide a valid state.
                  </div>
                </div>
                <div class="col-md-3 mb-3">
                  <label for="zip" class="form-label">
                    Zip
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="zip"
                    placeholder=""
                    required
                  />
                  <div class="invalid-feedback">Zip code required.</div>
                </div>
              </div>
              <hr class="mb-4" />
              <div class="form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="same-address"
                />
                <label class="form-check-label" for="same-address">
                  Shipping address is the same as my billing address
                </label>
              </div>
              <div class="form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="save-info"
                />
                <label class="form-check-label" for="save-info">
                  Save this information for next time
                </label>
              </div>
              <hr class="mb-4" />
              <h4 class="mb-3">Payment</h4>

              <div id="paypal-button-container"></div>
              <Link className="text-body" to="/Cart"><i className="fas fa-long-arrow-alt-left me-2"></i>Back to Cart</Link>
            </form>
            
          </div>
        </div>
      </div>
    </section>
  </div>
  );
}

export default Checkout



