import React, { useEffect, useState } from "react";
import Bounce from "react-reveal/Bounce";
import { Link } from "react-router-dom";
const useCountdown = (targetDate) => {
  const countDownDate = new Date(targetDate).getTime();

  const [countDown, setCountDown] = useState(
    countDownDate - new Date().getTime()
  );

  useEffect(() => {
    const interval = setInterval(() => {
      setCountDown(countDownDate - new Date().getTime());
    }, 1000);

    return () => clearInterval(interval);
  }, [countDownDate]);

  return getReturnValues(countDown);
};

const getReturnValues = (countDown) => {
  // calculate time left
  const days = Math.floor(countDown / (1000 * 60 * 60 * 24));
  const hours = Math.floor(
    (countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
  );
  const minutes = Math.floor((countDown % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((countDown % (1000 * 60)) / 1000);

  return [days, hours, minutes, seconds];
};

function Deal({ addToCart, addToFavorites }) {
  // Replace 'targetDate' with the actual target date you want to count down to
  const targetDate = "2023-12-31T23:59:59"; // Example target date

  // Use the 'useCountdown' custom hook
  const [days, hours, minutes, seconds] = useCountdown(targetDate);

  // Create a mock product object for demonstration
  const products = [
    {
      name: "Samsung Galaxy S21",
      image: "https://m.media-amazon.com/images/I/61ZNIHIEUqL._AC_UY218_.jpg",
      rating: 4.5,
      oldPrice: "$799",
      newPrice: "$699",
    },
    {
      name: "iPad Pro (2021)",
      image: "https://m.media-amazon.com/images/I/81MF389-9gS._AC_UY218_.jpg",
      rating: 4.5,
      oldPrice: "$799",
      newPrice: "$559",
    },
    {
      name: "Dell XPS 13",
      image: "https://m.media-amazon.com/images/I/710+gleyyaL._AC_UY218_.jpg",
      rating: 4.5,
      oldPrice: "$999",
      newPrice: "$950",
    },
  ];

  return (
    <div className="cont">
      <div className="head">
        <h2>Deal of The Day Don't miss</h2>
      </div>
      <div className="offers">
        {products.map((product, index) => (
          <div className="offer" key={index}>
            <Bounce>
              <div className="image">
                <img src={product.image} alt="" />
              </div>
              <div className="text">
                <h3>{product.name}</h3>
                <div className="icons">
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                </div>
                <div className="price">
                  <div className="old-price">{product.oldPrice}</div>
                  <div className="new-price">{product.newPrice}</div>
                </div>
                <div className="count-down">
                  <div>
                    <span>{days}</span>
                    <span>Days</span>
                  </div>
                  <div>
                    <span>{hours}</span>
                    <span>Hrs</span>
                  </div>
                  <div>
                    <span>{minutes}</span>
                    <span>Mins</span>
                  </div>
                  <div>
                    <span>{seconds}</span>
                    <span>Sec</span>
                  </div>
                </div>

                <Link to="/shop">Buy Now</Link>
              </div>
            </Bounce>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Deal;
