import React, { useState, useEffect } from "react";
import axios from "axios";
import { useLocation } from "react-router-dom";
import BasicExample from "../NavBar/Nav";
import Categories from "../Categories/Categories";
import { Footer } from "../Footer/Footer";
import Fade from "react-reveal/Fade";
function ShopComponent({
  cart,
  addToCart,
  favorites,
  addToFavorites,
  removeFromCart,
  removeFromFavorites,
}) {
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [products, setProducts] = useState([]);
  const [brands, setBrands] = useState([]);
  const [selectedLayout, setSelectedLayout] = useState("grid");
  const location = useLocation();
  const [showFilter, setShowFilter] = useState(false);
  const [searchInput, setSearchInput] = useState("");

  const [selectedBrands, setSelectedBrands] = useState([]);
  const [selectedRatings, setSelectedRatings] = useState([]);
  const [categoryClicked, setCategoryClicked] = useState(false);
  const [sortingOption, setSortingOption] = useState("high-to-low"); // Default sorting option
  const [message, setMessage] = useState("");
  const [isMessageVisible, setIsMessageVisible] = useState(false);
  // Placeholder data for brands and ratings (replace with actual data)
  const brandsData = ["Brand1", "Brand2", "Brand3"];
  const ratings = [5, 4, 3, 2, 1]; // Placeholder ratings

  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const categoryParam = searchParams.get("category");

    if (categoryParam) {
      setSelectedCategory(categoryParam);
      setCategoryClicked(true);
    } else {
      setSelectedCategory(null);
    }

    axios
      .get("https://api.jsonbin.io/v3/b/64ff06e0d972192679c1b50d")
      .then((response) => {
        const productData = response.data.record;
        setProducts(productData);

        // Extract unique brands and set it in state
        const brandsArray = [
          ...new Set(productData.map((product) => product.brand)),
        ];
        setBrands(brandsArray);
      })
      .catch((error) => console.error("Error fetching product data: ", error));
  }, [location]);

  useEffect(() => {
    if (categoryClicked) {
      filterProducts();
    }
  }, [categoryClicked, searchInput, selectedBrands, selectedRatings]);

  const renderStarIcons = (numericRating) => {
    const fullStars = Math.floor(numericRating);
    const hasHalfStar = numericRating - fullStars >= 0.25;

    const starIcons = [];
    for (let i = 0; i < fullStars; i++) {
      starIcons.push(<i key={i} className="fas fa-star"></i>);
    }
    if (hasHalfStar) {
      starIcons.push(<i key="half" className="fas fa-star-half-alt"></i>);
    }

    return starIcons;
  };
  const toggleFilter = () => {
    setShowFilter((prev) => !prev);
    const shop = document.getElementById("shop");
    if (shop) {
      shop.classList.toggle("toggle-show");
    }
  };

  // ... (previous code)

  const filterProducts = () => {
    let filtered = [...products];

    if (searchInput.trim() !== "") {
      filtered = filtered.filter((product) =>
        product.modelName.toLowerCase().includes(searchInput.toLowerCase())
      );
    }

    if (selectedCategory) {
      filtered = filtered.filter(
        (product) => product.category === selectedCategory
      );
    }

    if (selectedBrands.length > 0) {
      filtered = filtered.filter((product) =>
        selectedBrands.includes(product.brand)
      );
    }

    if (selectedRatings.length > 0) {
      filtered = filtered.filter((product) =>
        selectedRatings.includes(parseFloat(product.reviews))
      );
    }

    // Sort products based on the selected sorting option
    if (sortingOption === "high-to-low") {
      filtered.sort((a, b) => b.price - a.price); // Sort high to low
    } else if (sortingOption === "low-to-high") {
      filtered.sort((a, b) => a.price - b.price); // Sort low to high
    } else if (sortingOption === "latest") {
      // Implement sorting by the latest criteria
      // You should define your own sorting logic for "latest"
    } else if (sortingOption === "top-rated") {
      // Implement sorting by top-rated criteria
      // You should define your own sorting logic for "top-rated"
    } else if (sortingOption === "top-sellers") {
      // Implement sorting by top-sellers criteria
      // You should define your own sorting logic for "top-sellers"
    }

    return filtered;
  };

  // ... (rest of the code)

  const handleSearchInputChange = (e) => {
    const inputValue = e.target.value;
    setSearchInput(inputValue);
    if (categoryClicked) {
      setCategoryClicked(false);
    }
  };

  const handleCategoryFilterChange = (category) => {
    if (selectedCategory === category) {
      // If the same category is clicked again, unset the filter
      setSelectedCategory(null);
      setCategoryClicked(false);
    } else {
      setSelectedCategory(category);
      setCategoryClicked(true);
    }
  };

  const handleBrandChange = (brand) => {
    const updatedBrands = selectedBrands.includes(brand)
      ? selectedBrands.filter((b) => b !== brand)
      : [...selectedBrands, brand];
    setSelectedBrands(updatedBrands);
  };
  const handleRatingChange = (rating) => {
    if (selectedRatings.length === 1 && selectedRatings[0] === rating) {
      // If the same rating is already selected, clear the selection
      setSelectedRatings([]);
    } else {
      // Otherwise, clear previous selections and set the new rating as selected
      setSelectedRatings([rating]);
    }
  };

  const filteredProducts = filterProducts();

  const renderedProducts =
    filteredProducts.length > 0 ? (
      filteredProducts.map((product) => {
        const numericRating = parseFloat(product.reviews);
        const isStockAvailable = product.stock;
        return (
          <div key={product.id} className="product" id={`product${product.id}`}>
            <Fade top>
              <div className="image">
                <img src={product.image} alt="" />
              </div>
              <div className="text">
                <p>{product.category}</p>
                <h4>{product.modelName}</h4>
                <div className="icons">{renderStarIcons(numericRating)}</div>
                <div className="price">{product.price}</div>
              </div>
              <div className={`options ${isStockAvailable ? "in-stock" : ""}`}>
                <div
                  className="add-to-cart"
                  onClick={() => handleAddToCart(product)}
                >
                  <i className="fa-solid fa-cart-shopping"></i>
                </div>
                <div
                  className="add-to-fav"
                  onClick={() => handleAddToFavorites(product)}
                >
                  <i className="fa-solid fa-heart"></i>
                </div>
              </div>
              <div className={`stock ${isStockAvailable ? "in-stock" : ""}`}>
                {isStockAvailable ? "In Stock" : "Out Of Stock"}
              </div>
            </Fade>
          </div>
        );
      })
    ) : (
      <h2>No Products Found</h2>
    );
  const showMessage = (text) => {
    setMessage(text);
    setIsMessageVisible(true);

    // Hide the message after 5 seconds
    setTimeout(() => {
      setIsMessageVisible(false);
      setMessage("");
    }, 3000);
  };

  const handleAddToCart = (product) => {
    addToCart(product);
    showMessage(`Added ${product.modelName} to the cart`);
  };

  const handleAddToFavorites = (product) => {
    addToFavorites(product);
    showMessage(`Added ${product.modelName} to favorites`);
  };

  return (
    <div>
      <section className="shop">
        <BasicExample
          cart={cart}
          favorites={favorites}
          removeFromCart={removeFromCart}
          removeFromFavorites={removeFromFavorites}
          addToCart={addToCart}
          addToFavorites={addToFavorites}
        />
        <Categories
          selectedCategory={selectedCategory}
          onCategoryChange={handleCategoryFilterChange}
        />

        <div id="shop" className="shop-box">
          <div className={`filter ${showFilter ? "toggle-show" : ""}`}>
            <div className="filter-brand">
              <h4>Brands</h4>
              <ul className="brand-list">
                {brands.map((brand) => (
                  <li key={brand}>
                    <input
                      type="checkbox"
                      id={brand}
                      name="brand"
                      value={brand}
                      checked={selectedBrands.includes(brand)}
                      onChange={() => handleBrandChange(brand)}
                    />
                    <label htmlFor={brand}>{brand}</label>
                  </li>
                ))}
              </ul>
            </div>
            <div className="filter-rate">
              <h4>Ratings</h4>
              <ul className="rating-list">
                {ratings.map((rating) => (
                  <li
                    key={rating}
                    onClick={() => handleRatingChange(rating)}
                    className={
                      selectedRatings.includes(rating) ? "selected" : ""
                    }
                  >
                    {renderStarIcons(rating)}
                  </li>
                ))}
              </ul>
            </div>
          </div>
          <div className="toggle-btn" onClick={toggleFilter}>
            <i className="fa-solid fa-bars"></i>
          </div>
          <div className="products">
            <div className="show-options">
              <div className="icons">
                <i
                  className={`fa-solid fa-table-cells-large ${
                    selectedLayout === "grid" ? "selected-option" : ""
                  }`}
                  onClick={() => setSelectedLayout("grid")}
                ></i>
                <i
                  className={`fa-solid fa-bars ${
                    selectedLayout === "flex" ? "selected-option" : ""
                  }`}
                  onClick={() => setSelectedLayout("flex")}
                ></i>
              </div>
              <div className="search-container">
                <input
                  type="text"
                  className="search-input"
                  placeholder="Search..."
                  value={searchInput}
                  onChange={handleSearchInputChange}
                />
              </div>
              {/* <select
                value={sortingOption}
                onChange={(e) => setSortingOption(e.target.value)}
              >
                <option value="high-to-low">High Price to Low</option>
                <option value="low-to-high">Low Price to High</option>
              </select> */}
            </div>
            <div
              className={`products-container ${
                selectedLayout === "grid" ? "show-columns" : "show-rows"
              }`}
            >
              {renderedProducts}
            </div>
          </div>
        </div>
        <div className={`custom-message ${isMessageVisible ? "show" : ""}`}>
          {message}
        </div>
      </section>
      <Footer />
    </div>
  );
}

export default ShopComponent;
