import React, { useEffect, useState } from "react";
import Carousel from "react-bootstrap/Carousel";
import Fade from "react-reveal/Fade";
import { Link } from "react-router-dom";

function RandomCarousel() {
  const [items, setItems] = useState([]);
  const [selectedItems, setSelectedItems] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Fetch data from the API
    fetch("https://api.jsonbin.io/v3/b/64ff06e0d972192679c1b50d", {
      headers: {
        "X-Master-Key":
          "$2b$10$Zx6HKV9ZGo8/WljW5CdxUObbvYjN1FVMDgLQGp9pWllzgFWk7lYgG",
      },
    })
      .then((response) => response.json())
      .then((data) => {
        setItems(data.record);
        setIsLoading(false);
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
        setIsLoading(false);
      });
  }, []);

  useEffect(() => {
    // Randomly select items from the list
    const randomItems = items.sort(() => Math.random() - 0.5).slice(0, 3);
    setSelectedItems(randomItems);
  }, [items]);

  const handleSelect = (selectedIndex, e) => {
    // When the user selects a different item in the carousel, update the selected items
    const randomItems = items.sort(() => Math.random() - 0.5).slice(0, 3);
    setSelectedItems(randomItems);
  };

  if (isLoading) {
    return (
      <Carousel
        data-bs-theme="dark"
        className="slider"
        onSelect={handleSelect}
      >
        <Carousel.Item>
          <Fade left>
            <Carousel.Caption>
              <p>Starting at $300</p>
              <h1>Apple Watch Series 7</h1>
              <h4>Don't miss the chance</h4>
              <Link to="/shop" className="btn">
                Shop Now
              </Link>
            </Carousel.Caption>
          </Fade>
          <Fade right>
            <img
              className="d-block"
              src="https://m.media-amazon.com/images/I/71XohCtGXHL._AC_UY218_.jpg"
              alt="Apple Watch"
            />
          </Fade>
        </Carousel.Item>
      </Carousel>
    );
  }

  return (
    <Carousel
      data-bs-theme="dark"
      className="slider"
      onSelect={handleSelect}
    >
      {selectedItems.map((item) => (
        <Carousel.Item key={item.id}>
          <Fade left>
            <Carousel.Caption>
              <p>Starting at {item.price}</p>
              <h1>{item.modelName}</h1>
              <h4>Don't miss the chance</h4>
              <Link to="/shop" className="btn">
                Shop Now
              </Link>
            </Carousel.Caption>
          </Fade>
          <Fade right>
            <img className="d-block" src={item.image} alt={item.modelName} />
          </Fade>
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

export default RandomCarousel;
