import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import { Nav } from "react-bootstrap";
import { Link } from "react-router-dom";
import { onAuthStateChanged, signOut } from "firebase/auth";
import { auth } from "../Firebase/Firebase";

function BasicExample({
  cart,
  favorites,
  removeFromCart,
  removeFromFavorites,
  addToCart,
  addToFavorites,
}) {
  const [showCartDropdown, setShowCartDropdown] = useState(false);
  const [showFavoritesDropdown, setShowFavoritesDropdown] = useState(false);
  const [cartItems, setCartItems] = useState(cart || []);
  const [favoritesItems, setFavoritesItems] = useState(favorites || []);
  const [user, setUser] = useState(null);

  useEffect(() => {
    setCartItems(cart || []);
  }, [cart]);

  useEffect(() => {
    setFavoritesItems(favorites || []);
  }, [favorites]);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        setUser(user);
      } else {
        setUser(null);
      }
    });

    return () => unsubscribe();
  }, []);

  const handleLogout = () => {
    signOut(auth)
      .then(() => {
        window.location.href = "/";
        console.log("Signed out successfully");
      })
      .catch((error) => {
        console.error("Error signing out:", error.message);
      });
  };

  // Count the number of items in the cart
  const cartItemCount = Array.isArray(cartItems)
    ? cartItems.reduce((acc, product) => acc + product.quantity, 0)
    : 0;

  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <Navbar.Brand href="/">Thecnos</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to="/">
              Categories
            </Nav.Link>
            <Nav.Link as={Link} to="/shop">
              Shop
            </Nav.Link>
            <Nav.Link as={Link} to="/">
              Contact
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
        <div className="icons">
        <div className="icon-dropdown cart-icon">

  <Link to="/cart">
    <i className="fa-solid fa-cart-shopping cart">
    {cartItemCount > 0 && <span>{cartItemCount}</span>}
    </i>
  </Link>
</div>

          <div className="icon-dropdown">
<i
  className="fa-solid fa-heart fav"
  onClick={() => setShowFavoritesDropdown(!showFavoritesDropdown)}
>
  {Array.isArray(favoritesItems) && favoritesItems.length > 0 && (
    <span>{favoritesItems.length}</span>
  )}
</i>
{showFavoritesDropdown && Array.isArray(favoritesItems) && (
  <ul className="dropdown">
    {favoritesItems.map((product) => (
      <li key={product.id}>
        <div className="text">
          <h3>{product.modelName}</h3>
          <p>${product.price}</p>
        </div>
        <div className="image">
          <img src={product.image} alt={product.name} />
        </div>
        <i
          className="fa-solid fa-times remove-icon"
          onClick={() => removeFromFavorites(product.id)}
        ></i>
      </li>
    ))}
  </ul>
)}
</div>
          <div className="icon-dropdown user-drop">
            {user ? (
              <Link to="/" onClick={handleLogout}>
                Sign Out
              </Link>
            ) : (
              <Link to="/signin">Sign In</Link>
            )}
          </div>
        </div>
      </Container>
    </Navbar>
  );
}

export default BasicExample;
