import React from "react";
import Form from "./Form";

import Fade from "react-reveal/Fade";
function Contact() {
  return (
    <section id="Contact">
      <Fade top>
        <div className="heading animate-up">
          <h1>Contact US</h1>
        </div>
        <div className="container">
          <div className="contact animate-up">
            <div className="info">
              <h3>Contact Information</h3>
              <ul>
                <li>
                  <i className="fa-solid fa-phone"></i>01099452977
                </li>
                <li>
                  <i className="fa-solid fa-envelope"></i>info@thecnos.com
                </li>
                <li>
                  <i className="fa-solid fa-location-dot"></i>Nasr city, rozana
                  mall, next to sports arena1
                </li>
              </ul>
              <div className="icons">
                <a href="https://www.facebook.com/">
                  <i className="fa-brands fa-facebook"></i>
                </a>
                <a href="https://www.instagram.com/">
                  <i className="fa-brands fa-instagram"></i>
                </a>
                <a href="https://wa.me/01099452977">
                  <i className="fa-brands fa-whatsapp"></i>
                </a>
              </div>
            </div>
            <div className="send">
              <h3>Send Us Message</h3>
              <Form />
            </div>
          </div>
          <div className="map">
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m21!1m12!1m3!1d6907.365366232605!2d31.397544675203886!3d30.045960123087408!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!4m6!3e6!4m0!4m3!3m2!1d30.0473151!2d31.3969518!5e0!3m2!1sar!2seg!4v1687135987721!5m2!1sar!2seg"
              loading="lazy"
              referrerpolicy="no-referrer-when-downgrade"
            ></iframe>
          </div>
        </div>
      </Fade>
    </section>
  );
}

export default Contact;
