// ParentComponent.js
import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Route, Routes } from "react-router-dom";
import Trend from "../Trend/Trend";
import BasicExample from "../NavBar/Nav";
import Carousel from "../slider/Slider";
import Categories from "../Categories/Categories";
import Features from "../Features/Features";
import Deal from "../Deal/Deal";
import Contact from "../Contact/Contact";
import { Footer } from "../Footer/Footer";
import ShopComponent from "../Shop/Shop";
import Checkout from "../Checkout/Checkout";
import Loading from "../Loading/Loading";
import ChatBot from "../Chat/Chat";
import SignUp from "../SignUp/SignUp";
import SignIn from "../SignIn/SignIn";
import Cart from "../Cart/Cart";
import { auth } from "../Firebase/Firebase";

function ParentComponent() {
  const [cart, setCart] = useState([]);
  const [favorites, setFavorites] = useState([]);
  const [favoriteStatus, setFavoriteStatus] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false); // Initialize as false
  const navigate = useNavigate();
  const location = useLocation();

  // Load cart data from Local Storage when the component mounts
  useEffect(() => {
    const cartData = localStorage.getItem("cart");
    if (cartData) {
      setCart(JSON.parse(cartData));
    }
  }, []);

  useEffect(() => {
    const loadingTimeout = setTimeout(() => {
      setIsLoading(false);
    }, 2000);

    return () => clearTimeout(loadingTimeout);
  }, []);
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        // User is signed in
        setIsAuthenticated(true);
      } else {
        // User is not signed in
        setIsAuthenticated(false);
      }
    });

    return unsubscribe;
  }, []);

  const addToCart = (product) => {
    const existingProductIndex = cart.findIndex(
      (item) => item.id === product.id
    );
    if (existingProductIndex !== -1) {
      const updatedCart = [...cart];
      updatedCart[existingProductIndex].quantity += 1;
      setCart(updatedCart);
    } else {
      setCart([...cart, { ...product, quantity: 1 }]);
    }

    // Update local storage
    localStorage.setItem("cart", JSON.stringify(cart));
  };

  const removeFromCart = (productId) => {
    const updatedCart = cart.filter((item) => item.id !== productId);
    setCart(updatedCart);

    // Update local storage
    localStorage.setItem("cart", JSON.stringify(updatedCart));
  };
  const removeFromFavorites = (itemId) => {
    setFavorites((prevCart) =>
      prevCart.filter((product) => product.id !== itemId)
    );
  };

  const addToFavorites = (product) => {
    if (!favoriteStatus[product.id]) {
      setFavoriteStatus((prevStatus) => ({
        ...prevStatus,
        [product.id]: true,
      }));
      setFavorites((prevFavorites) => [...prevFavorites, product]);
    }
  };

  const updateCart = (updatedCart) => {
    setCart(updatedCart);
    localStorage.setItem("cart", JSON.stringify(updatedCart)); // Save to Local Storage
  };

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
        <Routes>
          <Route path="/signin" element={<SignIn />} />
          <Route path="/signup" element={<SignUp />} />
          <Route
            path="/"
            element={
              <section id="hero">
                <BasicExample
                  cart={cart}
                  favorites={favorites}
                  removeFromCart={removeFromCart}
                  removeFromFavorites={removeFromFavorites}
                  addToCart={addToCart}
                  addToFavorites={addToFavorites}
                />
                <section id="home">
                  <Carousel />
                  <section id="categories">
                    <Categories />
                  </section>
                  <Features />
                  <ChatBot></ChatBot>
                </section>
                <section id="trend">
                  <Trend
                    cart={cart}
                    favorites={favorites}
                    removeFromCart={removeFromCart}
                    removeFromFavorites={removeFromFavorites}
                    addToCart={addToCart}
                    addToFavorites={addToFavorites}
                  />
                </section>
                <section id="deal">
                  <Deal />
                </section>
                <Contact />
                <section id="footer">
                  <Footer />
                </section>
              </section>
            }
          />
          <Route
            path="/shop"
            element={
              <ShopComponent
                cart={cart}
                addToCart={addToCart}
                favorites={favorites}
                addToFavorites={addToFavorites}
                removeFromCart={removeFromCart}
                removeFromFavorites={removeFromFavorites}
              />
            }
          />
          <Route
            path="/checkout"
            element={
              <Checkout
                cart={cart}
                addToCart={addToCart}
                favorites={favorites}
                addToFavorites={addToFavorites}
                removeFromCart={removeFromCart}
                removeFromFavorites={removeFromFavorites}
                updateCart={updateCart}
                isAuthenticated={isAuthenticated}
              />
            }
          />
          <Route
            path="/cart"
            element={
              <Cart
                cart={cart}
                addToCart={addToCart}
                favorites={favorites}
                addToFavorites={addToFavorites}
                removeFromCart={removeFromCart}
                removeFromFavorites={removeFromFavorites}
                updateCart={updateCart} // Pass the updateCart function
              />
            }
          />
        </Routes>
      )}
    </>
  );
}

export default ParentComponent;
