import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { createUserWithEmailAndPassword, AuthErrorCodes } from "firebase/auth";
import { auth } from "../Firebase/Firebase";

const Signup = () => {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordError, setPasswordError] = useState(""); // State to store password error message
  const [emailError, setEmailError] = useState(""); // State to store email error message

  const onSignup = async (e) => {
    e.preventDefault();

    // Password validation: Check if the password is at least 6 characters
    if (password.length < 6) {
      setPasswordError("Password must be at least 6 characters long");
      return; // Prevent form submission
    }

    try {
      const userCredential = await createUserWithEmailAndPassword(
        auth,
        email,
        password
      );

      // User successfully signed up
      const user = userCredential.user;
      console.log("User signed up successfully:", user);

      // You can navigate to a different page after signing up if needed
      navigate("/signin");
    } catch (error) {
      const errorCode = error.code;
      const errorMessage = error.message;

      // Handle specific error codes
      if (errorCode === AuthErrorCodes.EMAIL_EXISTS) {
        setEmailError("Email is already in use. Please use a different email.");
      } else {
        setEmailError(""); // Clear email error message for other errors
      }

      console.error("Error signing up:", errorCode, errorMessage);
    }
  };

  return (
    <main className="user-form">
      <div className="demo-container">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-12 mx-auto">
              <div className="text-center image-size-small position-relative">
                <img
                  src="https://annedece.sirv.com/Images/user-vector.jpg"
                  className="rounded-circle p-2 bg-white"
                  alt="User"
                />
                <div className="icon-camera">
                  <a href="" className="text-primary">
                    <i className="lni lni-camera"></i>
                  </a>
                </div>
              </div>
              <div className="p-5 bg-white rounded shadow-lg">
                <h3 className="mb-2 text-center pt-5">Sign UP</h3>
                <form className="form-signup" onSubmit={onSignup}>
                  <label htmlFor="inputEmail" className="sr-only">
                    Email address
                  </label>
                  <input
                    type="email"
                    id="inputEmail"
                    className="form-control"
                    placeholder="Email address"
                    required
                    autoFocus
                    value={email}
                    onChange={(e) => {
                      setEmail(e.target.value);
                      setEmailError(""); // Clear email error message when the user types
                    }}
                  />
                  {emailError && <p className="text-danger">{emailError}</p>}
                  <label htmlFor="inputPassword" className="sr-only">
                    Password
                  </label>
                  <input
                    type="password"
                    id="inputPassword"
                    className="form-control"
                    placeholder="Password"
                    required
                    value={password}
                    onChange={(e) => {
                      setPassword(e.target.value);
                      setPasswordError(""); // Clear password error message when the user types
                    }}
                  />
                  {passwordError && (
                    <p className="text-danger">{passwordError}</p>
                  )}
                  <button
                    className="btn btn-primary btn-lg w-100 shadow-lg sign-up-btn"
                    type="submit"
                  >
                    SIGN UP
                  </button>
                </form>
                <div className="text-center pt-4">
                  <p className="m-0">
                    You have an account? <NavLink to="/signin">Sign In</NavLink>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default Signup;
