import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Zoom from 'react-reveal/Zoom';
function Features() {
  // Sample feature data
  const featureData = [
    {
      featureIcon: <i class="fas fa-truck"></i>,
      featureTitle: "Free Delivery",
      featureDescription: "Orders from all items",
    },
    {
      featureIcon: <i class="fas fa-dollar-sign"></i>,
      featureTitle: "Return & Refund",
      featureDescription: "Money back guarantee",
    },
    {
      featureIcon: <i class="fas fa-percentage"></i>,
      featureTitle: "Member Discount",
      featureDescription: "One very order over $140.00",
    },
    {
      featureIcon: <i class="fas fa-headphones"></i>,
      featureTitle: "Support 24/7",
      featureDescription: "Contact us 24 hours a day",
    },
    // Add more feature data as needed
  ];

  return (
    <div className="features-container cont">
      {featureData.map((feature, index) => (
        <div className="feature" key={index}>
          <Zoom top>
            {" "}
            <div className="feature-content">
              <div className="">{feature.featureIcon}</div>
              <div className="text">
                <h3 className="">{feature.featureTitle}</h3>
                <p>{feature.featureDescription}</p>
              </div>
            </div>
          </Zoom>
        </div>
      ))}
    </div>
  );
}

export default Features;
