import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { auth } from '../Firebase/Firebase';

function Cart({ cart, removeFromCart, updateCart }) {
  const [cartItems, setCartItems] = useState([]);
  const [message, setMessage] = useState('');
  const [isMessageVisible, setIsMessageVisible] = useState(false);
  const navigate = useNavigate();

  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      setIsAuthenticated(!!user);
    });

    return unsubscribe;
  }, []);

  useEffect(() => {
    // Load the cart from local storage when the component mounts
    const savedCart = localStorage.getItem('cart');
    if (savedCart) {
      setCartItems(JSON.parse(savedCart));
    }
  }, []);

  const totalPrice = Array.isArray(cartItems)
    ? cartItems.reduce(
        (acc, product) =>
          acc + parseFloat(product.price.replace('$', '')) * product.quantity,
        0
      )
    : 0;

  const showMessage = (text) => {
    setMessage(text);
    setIsMessageVisible(true);

    setTimeout(() => {
      setIsMessageVisible(false);
      setMessage('');
    }, 5000);
  };

  const updateQuantity = (productId, quantity) => {
    const updatedCart = cartItems.map((product) => {
      if (product.id === productId) {
        const newQuantity = Math.max(0, product.quantity + quantity);

        if (newQuantity === 0) {
          handleRemoveFromCart(productId);
        } else {
          return { ...product, quantity: newQuantity };
        }
      }
      return product;
    });

    setCartItems(updatedCart);
    updateCart(updatedCart);
  };

  const handleRemoveFromCart = (productId) => {
    const productName = cartItems.find((item) => item.id === productId)
      .modelName;
    const updatedCart = cartItems.filter((item) => item.id !== productId);

    setCartItems(updatedCart);
    updateCart(updatedCart);

    showMessage(`Removed ${productName} from the cart`);
  };
  const totalQuantity = Array.isArray(cartItems)
    ? cartItems.reduce((acc, product) => acc + product.quantity, 0)
    : 0;
  return (
    <section className="h-custom" style={{ backgroundColor: '#eee' }}>
      <div className="container py-5 h-100">
        <div className="row d-flex justify-content-center align-items-center h-100">
          <div className="col-12">
            <div
              className="card card-registration card-registration-2"
              style={{ borderRadius: '15px' }}
            >
              <div className="card-body p-0">
                <div className="row g-0">
                  <div className="col-lg-8">
                    <div className="p-5">
                      <div className="d-flex justify-content-between align-items-center mb-5">
                        <h1 className="fw-bold mb-0 text-black">Shopping Cart</h1>
                        <h6 className="mb-0 text-muted">{totalQuantity} items</h6>
                      </div>
                      <hr className="my-4" />
                      {cartItems.map((product, index) => (
                        <div
                          className="row mb-4 d-flex justify-content-between align-items-center cart-item"
                          key={index}
                        >
                          <div className="col-md-2 col-lg-2 col-xl-2">
                            <img
                              src={product.image}
                              className="img-fluid rounded-3"
                              alt={product.name}
                            />
                          </div>
                          <div className="col-md-3 col-lg-3 col-xl-3">
                            <h6 className="text-muted">{product.category}</h6>
                            <h6 className="text-black mb-0">{product.modelName}</h6>
                          </div>
                          <div className="col-md-3 col-lg-3 col-xl-2 d-flex">
                            <button
                              className="btn btn-link px-2"
                              onClick={() => updateQuantity(product.id, -1)}
                            >
                              <i className="fas fa-minus"></i>
                            </button>
                            <span className="quantity-value">{product.quantity}</span>
                            <button
                              className="btn btn-link px-2"
                              onClick={() => updateQuantity(product.id, 1)}
                            >
                              <i className="fas fa-plus"></i>
                            </button>
                          </div>
                          <div className="col-md-3 col-lg-2 col-xl-2 offset-lg-1">
                            <h6 className="mb-0">{product.price}</h6>
                          </div>
                          <div className="col-md-1 col-lg-1 col-xl-1 text-end">
                            <a
                              href="#!"
                              className="text-muted"
                              onClick={() => handleRemoveFromCart(product.id)}
                            >
                              <i className="fas fa-times"></i>
                            </a>
                          </div>
                        </div>
                      ))}
                      <hr className="my-4" />
                      <div className="pt-5">
                        <h6 className="mb-0">
                          <Link to="/shop" className="text-body">
                            <i className="fas fa-long-arrow-alt-left me-2"></i>
                            Back to shop
                          </Link>
                        </h6>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4 bg-grey">
                    <div className="p-5">
                      <h3 className="fw-bold mb-5 mt-2 pt-1">Summary</h3>
                      <hr className="my-4" />
                      <div className="d-flex justify-content-between mb-4">
                        <h5 className="text-uppercase">Items {cartItems.length}</h5>
                        <h5>$ {totalPrice}</h5>
                      </div>
                      <h5 className="text-uppercase mb-3">Enter Code</h5>
                      <div className="mb-5">
                        <div className="form-outline">
                          <input
                            type="text"
                            id="form3Examplea2"
                            className="form-control form-control-lg"
                          />
                        </div>
                      </div>
                      <hr className="my-4" />
                      <div className="d-flex justify-content-between mb-5">
                        <h5 className="text-uppercase">Total price</h5>
                        <h5>$ {totalPrice}</h5>
                      </div>
                      {isAuthenticated ? (
                        <Link to="/checkout" className="btn btn-dark btn-block btn-lg">
                          Checkout
                        </Link>
                      ) : (
                        <button
                          onClick={() => {
                            navigate('/signup');
                          }}
                          className="btn btn-dark btn-block btn-lg"
                        >
                          Sign Up to Checkout
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={`custom-message ${isMessageVisible ? 'show' : ''}`}>
          {message}
        </div>
      </div>
    </section>
  );
}

export default Cart;
