import React, { useState } from "react";
import { signInWithEmailAndPassword, getAuth } from "firebase/auth";
import { NavLink, useNavigate } from "react-router-dom";
import { signInWithPopup, GoogleAuthProvider } from "firebase/auth";

const SignIn = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");

  const auth = getAuth(); // Initialize auth

  const onLogin = (e) => {
    e.preventDefault();
    setEmailError("");
    setPasswordError("");

    signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        navigate("/");
        console.log(user);
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;

        if (errorCode === "auth/user-not-found") {
          setEmailError("User not found. Please check your email.");
        } else if (errorCode === "auth/wrong-password") {
          setPasswordError("Wrong password. Please try again.");
        } else {
          console.log(errorCode, errorMessage);
        }
      });
  };

  // Function to handle Google sign-in
  const signInWithGoogle = async () => {
    const provider = new GoogleAuthProvider();
    try {
      await signInWithPopup(auth, provider); // Use signInWithPopup
      navigate("/");
    } catch (error) {
      console.error("Google sign-in error:", error);
    }
  };

  return (
    <main className="user-form">
      <div className="demo-container">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-12 mx-auto">
              <div className="text-center image-size-small position-relative">
                <img
                  src="https://annedece.sirv.com/Images/user-vector.jpg"
                  className="rounded-circle p-2 bg-white"
                  alt="User"
                />
                <div className="icon-camera">
                  <a href="" className="text-primary">
                    <i className="lni lni-camera"></i>
                  </a>
                </div>
              </div>
              <div className="p-5 bg-white rounded shadow-lg">
                <h3 className="mb-2 text-center pt-5">Sign In</h3>
                <form className="form-signin" onSubmit={onLogin}>
                  <label htmlFor="inputEmail" className="sr-only">
                    Email address
                  </label>
                  <input
                    type="email"
                    id="inputEmail"
                    className="form-control"
                    placeholder="Email address"
                    required
                    autoFocus
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                  {emailError && <p className="text-danger">{emailError}</p>}
                  <label htmlFor="inputPassword" className="sr-only">
                    Password
                  </label>
                  <input
                    type="password"
                    id="inputPassword"
                    className="form-control"
                    placeholder="Password"
                    required
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                  {passwordError && (
                    <p className="text-danger">{passwordError}</p>
                  )}
                  <p className="m-0 py-4">
                    <a href="" className="text-muted">
                      Forgot password?
                    </a>
                  </p>
                  <button className="btn btn-primary btn-lg w-100 shadow-lg">
                    SIGN IN
                  </button>

                  <button
                    onClick={signInWithGoogle}
                    className="google-sign-in-button btn btn-primary btn-lg w-100 shadow-lg"
                  >
                    Sign in with Google
                  </button>
                </form>
                <div className="text-center pt-4">
                  <p className="m-0">
                    Do not have an account?{" "}
                    <NavLink to="/signup">Sign UP</NavLink>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default SignIn;
