import React, { useEffect, useState } from "react";
import axios from "axios";
import Fade from "react-reveal/Fade";
function Trend({ cart, addToCart, favorites, addToFavorites }) {
  const [selectedOption, setSelectedOption] = React.useState("top-rated");
  const [products, setProducts] = React.useState([]);

  useEffect(() => {
    axios
      .get("https://api.jsonbin.io/v3/b/64ff06e0d972192679c1b50d")
      .then((response) => {
        setProducts(response.data.record);
      })
      .catch((error) => console.error("Error fetching product data: ", error));
  }, []);
  const [message, setMessage] = useState("");
  const [isMessageVisible, setIsMessageVisible] = useState(false);
  // Function to render star icons based on a numeric rating
  const renderStarIcons = (numericRating) => {
    const fullStars = Math.floor(numericRating);
    const hasHalfStar = numericRating - fullStars >= 0.25;

    const starIcons = [];
    for (let i = 0; i < fullStars; i++) {
      starIcons.push(<i key={i} className="fas fa-star"></i>);
    }
    if (hasHalfStar) {
      starIcons.push(<i key="half" className="fas fa-star-half-alt"></i>);
    }

    return starIcons;
  };

  const filteredProducts = () => {
    switch (selectedOption) {
      case "top-rated":
        return products
          .sort((a, b) => parseFloat(b.reviews) - parseFloat(a.reviews))
          .slice(0, 9);
      case "top-sellers":
        return products.sort((a, b) => b.sellers - a.sellers).slice(0, 9);
      case "latest":
        return products
          .sort((a, b) => new Date(b.dateAdded) - new Date(a.dateAdded))
          .slice(0, 9);
      default:
        return products;
    }
  };

  const renderedProducts = filteredProducts().map((product) => {
    const numericRating = parseFloat(product.reviews);

    return (
      <Fade big>
        <div key={product.id} className="product" id={`product${product.id}`}>
          <div className="image">
            <img src={product.image} alt="" />
          </div>
          <div className="text">
            <p>{product.category}</p>
            <h4>{product.modelName}</h4>
            <div className="icons">
              {/* Render star icons based on rating */}
              {renderStarIcons(numericRating)}
            </div>
            <div className="price">{product.price}</div>
          </div>
          {/* Render stock status here */}
          <div className="options">
            <div
              className="add-to-card"
              onClick={() => handleAddToCart(product)}
            >
              <i className="fa-solid fa-cart-shopping"></i>
            </div>
            <div
              className="add-to-fav"
              onClick={() => handleAddToFavorites(product)}
            >
              <i className="fa-solid fa-heart"></i>
            </div>
          </div>
        </div>
      </Fade>
    );
  });
  const showMessage = (text) => {
    setMessage(text);
    setIsMessageVisible(true);

    // Hide the message after 5 seconds
    setTimeout(() => {
      setIsMessageVisible(false);
      setMessage("");
    }, 3000);
  };

  const handleAddToCart = (product) => {
    addToCart(product);
    showMessage(`Added ${product.modelName} to the cart`);
  };

  const handleAddToFavorites = (product) => {
    addToFavorites(product);
    showMessage(`Added ${product.modelName} to favorites`);
  };
  return (
    <div className="cont">
      <div className="head">
        <h3>Trending Products</h3>
        <select
          id="sort-options"
          onChange={(e) => setSelectedOption(e.target.value)}
          value={selectedOption}
        >
          <option value="top-rated">Top Rated</option>
          <option value="top-sellers">Top Sellers</option>
          <option value="latest">Latest</option>
        </select>
      </div>
      <div className="products-container">{renderedProducts}</div>
      <div className={`custom-message ${isMessageVisible ? "show" : ""}`}>
        {message}
      </div>
    </div>
  );
}

export default Trend;
